.section__hero-header {
  
  padding-top: 6px;
  margin: 0 auto;
  height: 502px;
  max-width: 1600px;

  background-color: #ffb8ca;
  
  overflow: hidden;

  @media screen and (min-width: 768px){
    padding-top: 38px;
    height: 399px;
  }
  @media screen and (min-width:1280px){
    padding-top: 38px;
    height: 655px;
  }
  
}
.container{
  position: relative;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  z-index: 100;
  @media screen and (min-width: 768px){}

  
}
.header__btn__box{
  display: flex;
  align-items: center;

}
.burger__btn {
  padding: 0;
  line-height: 0;

  background-color: transparent;
  cursor: pointer;
  border: none;
  color: var(--accent-color);

  @media screen and (min-width: 768px) {
    margin-right: 30px;
  }

  @media screen and (min-width: 1280px) {
    display: none;
  }
}
.burger__icon{
  fill: currentColor
}



.header__site-nav {
  display: flex;
  align-items: center;
}
.header__logo {
  @media screen and (min-width: 1280px) {
    position: absolute;
    left: -70px;;
  }
}
.header__list {
  margin-left: 240px;
  @media screen and (max-width: 1279px) {
    display: none;
  }
}
.header__item  {
  &+&{
    margin-left: 35px;
  }
}
.header__link{
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;
  color: var(--primary-light-text-color);
  transition: hover 250ms cubic-bezier(0.4, 0, 0.2, 1),
   focus 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
  &:focus,
  &:hover{
    color: var(--accent-color);
  }
  &.current{
    color: var(--accent-color);
  }
}

.header__btn {
  display: flex;
  align-items: center;

 

  &.button-white{
  
    padding-left: 37px;
    padding-right: 37px;
    
    font-size: 16px;
    line-height: 1.31px;
    @media screen and (min-width: 768px) {
      padding: 17px 37px;
    }
  }
  
  @media screen and (max-width: 767px) {
    display: none;
  }
  
  &__icon{
    margin-left: 12px;
  }
}
.header-btn__icon{
  margin-left: 12px;
}

//----- mobile menu--------//


.mobile-menu{
  position: fixed;
  z-index: 100;
  top: 0;
  right: 0;
  transform: translateX(100%);

  width: 250px;
  height: 100vh;

  padding-top: 60px;

  background-color: var(--menu-bg-color);

  opacity: 0;
  visibility: hidden;
  pointer-events: none;

  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
   visibility 250ms cubic-bezier(0.4, 0, 0.2, 1), transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

   
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  transform: translateX(0);
}

.mobile-menu__close-btn{
  position: absolute;
  top: 23px;
  right: 28px;
}
.mobile-menu__btn.button-white{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  
  margin: 0 auto;
  margin-bottom: 19px;

  padding-left: 49px;
  padding-right: 49px;

  font-weight: bold;
  font-size: 16px;
  line-height: 21px;
}
.btn-icon{
  
margin-left: 25px;
}
.mobile-menu__list{
  display: flex;
  flex-direction: column;
  
 

}
.mobile-menu__item{
  border-top: 1px solid #E18298;

  &:last-child{
    border-bottom: 1px solid #E18298; ;
  }
}
.mobile-menu__link{
  display: block;
  
  padding-top: 11px;
  padding-bottom: 12px;
  padding-left: 21px;

  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  color: var(--primary-light-text-color);

  transition: hover 250ms cubic-bezier(0.4, 0, 0.2, 1),
   focus 250ms cubic-bezier(0.4, 0, 0.2, 1);
   
  &:hover,
  &:focus{
    color: var(--accent-color);
  }
  &.current{
    color: var(--accent-color)
  }
}


//----- HERO--------//

.hero-container {

}

.hero__box{
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-top: 15px;

  @media screen and (min-width: 1280px){
   margin-top: 84px;
  }
}

.hero__box__title {
  @media screen and (max-width: 767px){
  position: absolute;
  top: 193px;
  left: 20px;
  z-index: 14;  
  }
  @media screen and (min-width: 768px){
    margin-bottom: 50px;

  }

  @media screen and (min-width: 1280px){
    margin-bottom: 112px;

  }
  
  
 
}
.hero__title {
  margin-bottom: 19px;
  
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--primary-light-text-color);

  @media screen and (min-width: 768px) {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 1.14;

  @media screen and (min-width: 1280px){
    margin-bottom: 25px;

    font-size: 38px;
    line-height: 1.16;

  }
  
    
  }
}
.hero__title-item {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  line-height: 96.5%;
  text-transform: uppercase;
  color: #d41443;
  
  @media screen and (min-width: 768px){
    
    font-size: 34px;
    line-height: 90.5%;
    letter-spacing: 0.04em;
    text-transform: uppercase;

  }


  @media screen and (min-width: 1280px){
    
    font-size: 58px;
    line-height: 90.5%;
    letter-spacing: 0.04em;

  }
}
.hero__buttons{
  display: flex;
}
.hero__button{
  @media screen and (max-width: 1279px){
    margin-right: 8px;
    &:last-child{
      margin-right: 0;
    }
  }
  @media screen and (min-width: 1280px){
    margin-right: 15px;
    &:last-child{
      margin-right: 0;
    }
  }
}

.hero__btn-light{
  display: inline-block;

  &.button-white{
    @media screen and (max-width: 767px) {
    
      padding: 9px 14px;
    }
    @media screen and (min-width: 768px) {
    
      padding: 6px 10px;
    }
    @media screen and (min-width: 1280px) {
      padding: 12px 20px;

      font-size: 14px;
      line-height: 1.29;
    }
  }
  
}
.hero__btn-dark{
  display: inline-block;
    
  &.button-red{
    @media screen and (max-width: 767px) {
    
      padding: 9px 14px;
    }
    @media screen and (min-width: 768px) {
    
      padding: 6px 10px;
    }
  
  @media screen and (min-width: 1280px) {
      padding: 12px 20px;
      
      font-size: 14px;
      line-height: 1.29;
    }
  }
  
     
}
.hero__contain__image {
  @media screen and (max-width: 767px) {
    position: absolute;
    top: 92px;
    right: 32px;
    z-index: 4;
    &::before{
      content: '';
      width: 346px;
      height: 344px;
      border-radius: 50%;
      background-color: var(--hero-circle-bg-color);
      display: block;
      position: absolute;
      top: -9px;
      right: -94px;
      z-index: -1;
    }
  }
  @media screen and (min-width: 768px) {
    position: absolute;
    top: 24px;
    right: 231px;
    z-index: 4;
    &::before{
      content: '';
      width: 324px;
      height: 322px;
      border-radius: 50%;
      background-color: var(--hero-circle-bg-color);
      display: block;
      position: absolute;
      top: -31px;
      right: -108px;
      z-index: -1;
    }
  }
  @media screen and (min-width: 1280px) {
    position: absolute;
    top: 60px;
    right: 331px;
    z-index: 4;
    &::before{
      content: '';
      width: 538px;
      height: 538px;
      border-radius: 50%;
      background-color: var(--hero-circle-bg-color);
      display: block;
      position: absolute;
      top: -50px;
      right: -183px;
      z-index: -1;
    }
  }
  
}


.hero__retail__list {
  @media screen and (max-width: 767px) {
    display: none;
  }
}
.hero__retail__item {
  & + & {
    @media screen and (min-width: 768px) {
    margin-top: 13px;
    }
  }
  & + & {
    @media screen and (min-width: 1288px) {
    margin-top: 15px;
    }
  }
 
}
.hero__retail__count {
  position: relative;
  z-index: 2;
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--accent-color);
  &::before{
    content: "";
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: var(--primary-light-text-color);
    display: block;
    position: absolute;
    top: -1px;
    right: -8px;
    z-index: -1;

  }
  @media screen and (min-width: 1280px) {
    position: relative;
    z-index: 2;
    font-size: 46px;
    line-height: 1.15;
    &::before{
    content: "";
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: var(--primary-light-text-color);
    display: block;
    position: absolute;
    top: -5px;
    right: -15px;
    z-index: -1;

  }
  }

}
.hero__retail__text {
  
  font-weight: normal;
  font-size: 8px;
  line-height: 1.25;
  color: var(--primary-light-text-color);

  @media screen and (min-width: 1280px) {
    font-weight: normal;
    font-size: 14px;
    line-height: 1.29;
  }
}

.hero__description__box {
  @media screen and (max-width: 767px) {
    display: none;
  }
  @media screen and (min-width: 768px) {
    margin-left: 120px;
  }
  @media screen and (min-width: 1280px) {
    margin-left: 198px;
  }
}



.hero__description__text {
  
  font-family: DM Sans;
font-style: normal;
font-weight: normal;
font-size: 8px;
line-height: 1.5;
color: var(--primary-light-text-color);

@media screen and (min-width: 768px) {
    margin-bottom: 7px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 1.4;
  }
}
.hero__description__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: var(--primary-light-text-color);

  @media screen and (min-width: 1280px) {
    width: 26px;
    height: 26px;
  }
}
.btn__icon {
  fill: var(--accent-color);
}


.hero__decor__list {
  @media screen and (max-width: 767px) {
    display: none;
  }
}
.hero__decor__item{

}
.hero__decor__image-milk {
    position: absolute;
    left: 20px;
    bottom: -12px;
    @media screen and (max-width: 767px) {
    display: none;
  }

    @media screen and (min-width: 1280px) {
    position: absolute;
    left: -20px;
    bottom: -12px;
  }
}

.hero__decor__image-girl {
    position: absolute;
    bottom:  -38px;
    right: 20px;
    z-index: 8;

    @media screen and (max-width: 767px) {
    display: none;
  }


    @media screen and (min-width: 1280px) {
    position: absolute;
    bottom: -36px;
    right: 7px;
    z-index: 8;
  }
    
}
